import React, { useState } from 'react';
import axios from 'axios';


function Det({ rowsA, columnsA, matrixA, name, setValidationError }) {

    const [result, setResult] = useState('');
    const [error, setError] = useState(null);


    const handleSubmit = async (event) => {
  

        if (rowsA != columnsA) {
            setValidationError(
                "Macierz musi by kwadratowa!"
            );
        } else {

        try {
            const response = await axios.post('http://localhost:8081/api/linalg/det', {
               rows: rowsA,
               columns: columnsA,
               matrix: matrixA,
            });
   
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            
            else {
            setResult(response.data);
            setValidationError('');
            }
    
        } catch (error) {
            setError(error.message);
        }
    
    };
}

    return (
        <div>
            <button onClick={handleSubmit}>det{name}</button> = {result}
        </div>


    )

}


export default Det;