import React from 'react';
import Combinatorics from './Combinatorics/Combinatorics';
import NumberTheory from './NumberTheory/NumberTheory';
import LinearAlgebra from './LinearAlgebra/LinearAlgebra';
import NumericalSystems from './NumericalSystems/NumericalSystems';
import Main from './main';

function Menu({option, setOption}) {

    
    if (option == "") {
        return (
          <Main setOption={setOption}/>
        );
    }

    else if (option == "Combinatorics") {
        return (
            <Combinatorics />
        );
    }

    else if (option == "NumberTheory") {
        return (
            <NumberTheory />
        );
    }

    else if (option == "LinearAlgebra") {
        return (
            <LinearAlgebra />
        );
    }

    else if (option == "NumericalSystems") {
        return (
            <NumericalSystems />
        );
    }

}

export default Menu;