import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function Operations() {
    const [result, setResult] = useState('');


    const formik = useFormik({

        initialValues: {
            num1: '',
            num2: '',
            symbol: '+',
        },
        validationSchema: Yup.object().shape({
            num1: Yup.string()
            .matches(/^[01]*$/, 'Wprowad poprawne liczby binarne')
            .required('Wprowad poprawne liczby binarne'),
            num2: Yup.string()
            .matches(/^[01]*$/, 'Wprowad poprawne liczby binarne')
            .required('Wprowad poprawne liczby binarne'),
        }),

        onSubmit: async values => {

            console.log(values);

            try {

            const response = await axios.post('http://localhost:8081/api/numsys/op',values);

            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            else {
            setResult(response.data);
            }
            }
            catch (error) {
                setError(error.message);
            }
        }

    });

    return (
        <div className="onepiece">
            <h3>Dziaania na liczbach binarnych</h3>
     
            <form onSubmit={formik.handleSubmit}>
     
            <input type="text" id="num1" value={formik.values.num1} onChange={formik.handleChange} />
         
            <select id="symbol" name="symbol" value={formik.values.symbol} onChange={formik.handleChange}>
                <option value='+'>+</option>
                <option value='-'>-</option>
                <option value='x'>x</option>
                <option value='/'>/</option>
            </select>
            <input type="text" id="num2" value={formik.values.num2} onChange={formik.handleChange} onBlur={formik.handleBlur} /> <div class="eq">=</div> 
    
            {result}
            <br/>
            <input type="submit"/><button type="button" onClick={formik.handleReset}>Resetuj</button>
            {formik.touched.num1 && formik.errors.num1 ? (
            <div className="error">{formik.errors.num1}</div>
            ) : null}
            {formik.touched.num2 && formik.errors.num2 ? (
            <div className="error">{formik.errors.num2}</div>
            ) : null}
            </form>
            {/* {error && <div>Error: {error}</div>} */}

        </div>
    );
}

export default Operations;
