import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function ConversionsInR() {
    const [result, setResult] = useState('');
    const [error, setError] = useState(null);

    const formik = useFormik({

        initialValues: {
            base1: 2,
            base2: 10,
            string: '',
        },
        validationSchema: Yup.object().shape({
            string: Yup.string()
            .required("Wpisz liczb")
            .test('valid-characters', "Wpisz poprawn liczb", (value, context) => {
                // if (!value) return true; // Allow empty input
                const base = context.parent.base1;
                let regex;
                if (base === 2) {
                    regex = /^[01.,]*$/;
                }
                else {
                    regex = /^[0-9.,]*$/;
                }

                return regex.test(value);
              }),
            }),
    
        onSubmit: async values => {

            try {

            const response = await axios.post('http://localhost:8081/api/numsys/doubleconv',values);

            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            else {
            setResult(response.data);
            }
            }
            catch (error) {
                setError(error.message);
            }
        }

    });

    return (
        <div className="onepiece">
            <h3>Konwersja liczb rzeczywistych</h3>
            <form onSubmit={formik.handleSubmit}>
                <div className="three_columns">
                    <div className="column">
                        <select id="base1" name="base1" value={formik.values.base1} onChange={formik.handleChange}>
                        <option disabled selected>wybierz baz</option>
                            <option value="2">2</option>
                            <option value="10">10</option>
                        </select><br/><br />
                        <input type="text" id="string" name="string" value={formik.values.string} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                    </div>
                    <div className="middle_column">
                        &rarr;
                        <br/><br/>
                        &rarr;
                    </div>
                    <div className="column">
                    <select id="base2" name="base2" value={formik.values.base2} onChange={formik.handleChange}>
                    <option disabled selected>wybierz baz</option>
                        <option value="2">2</option>                    
                            <option value="10">10</option>
                        </select>
                        <br/><br/>
                        {result}
                    </div>
                </div>
                <br/>
                <input type="submit"/>
                <button type="button" onClick={formik.handleReset}>Resetuj</button>
                {formik.touched.string && formik.errors.string ? (
            <div className="error">{formik.errors.string}</div>
            ) : null}
            </form>
            {/* {error && <div>Error: {error}</div>} */}
        </div>
    );
}

export default ConversionsInR;
