import React from 'react';
import axios from 'axios';


function Addition ({rowsA, columnsA, matrixA, rowsB, columnsB, matrixB, resultMatrix, setResultMatrix, setValidationError}) {

    const handleSubmit = async (data) => {
        
        if(rowsA != rowsB || columnsA != columnsB) {
            setValidationError("Macierze musz mie takie same wymiary");
        }
        else {

        try {
            const response = await axios.post('http://localhost:8081/api/linalg/addition', {

            matrixA: {
                rows: rowsA,
                columns: columnsA,
                matrix: matrixA, 
            },
            matrixB: {
               rows: rowsB,
               columns: columnsB,
               matrix: matrixB,
            }
            });
   
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            
            else {
                setResultMatrix(response.data);
            setValidationError('');
            }
    
        } catch (error) {
            setError(error.message);
        }
            
        
    }
    };



    return (

        <div>

            <button onClick={handleSubmit}>A+B</button>
            
        </div>

    )

}

export default Addition;