import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function Inverse() {

    const [result, setResult] = useState("  ");
    const [error,setError] = useState(null);

    const gcd = (a, b) => {
        while (b !== 0) {
          let temp = b;
          b = a % b;
          a = temp;
        }
        return a;
      };

    const formik = useFormik({

        initialValues: {
            a: '',
            b: '',
        },
        validationSchema: Yup.object().shape({
            a: Yup.number()
            .required("Wprowad warto a")
            .min(0,"Wprowad waciw warto a")
            .integer("Wprowad warto cakowit a")
            .test('valid-a-and-b', "Wprowad waciwe wartoci a i b", function(value) {
                const { b } = this.parent;
                return value < b;
              })
              .test('is-coprime', "a i b musz by wzgldnie pierwsze", function(value) {
                const { b } = this.parent;
                return gcd(value, b) === 1;
              }),
              b: Yup.number()
            .required("Wprowad warto b")
            .positive("Wprowad dodatni warto b")
            .integer("Wprowad warto cakowit b"),
          }),
        onSubmit: async values => {


                try {
                const response = await axios.post('http://localhost:8081/api/numbertheory/inverse', values);
                if (response.status !== 200) {
                    throw new Error('Failed to fetch data');
                }
                
                else {
                setResult(response.data);
                }
        
            } catch (error) {
                setError(error.message);
            }
        
        },        
    });

    return (
    <div className="inverse"  class="onepiece">
       <h3>Element odwrotny</h3>
    <form onSubmit={formik.handleSubmit}>
        Element odwrotny do <input type="number" id="a" name="a" value={formik.values.a} onChange={formik.handleChange} onBlur={formik.handleBlur} /> w Z<sub>
    <input type="number" id="b" name="b" value={formik.values.b} onChange={formik.handleChange}  onBlur={formik.handleBlur} /></sub> to {result}
    <input type="submit" /><button type="button" onClick={formik.handleReset}>Resetuj</button>
    </form>
    {formik.touched.a && formik.errors.a ? (
            <div className="error">{formik.errors.a}</div>
            ) : null}
            {formik.touched.b && formik.errors.b ? (
            <div className="error">{formik.errors.b}</div>
            ) : null}
</div>
);

}

export default Inverse;