import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function Permutation() {

    const [result, setResult] = useState("  ");


    const formik = useFormik({
        initialValues: {
          n: '',
        },
        validationSchema: Yup.object().shape({
          n: Yup.number()
          .required("Wprowad warto n!")
          .positive("Wprowad warto dodatni!")
          .integer("Wprowad warto cakowit!"),
        }),
        onSubmit: async values => {

           
              try {
            const response = await axios.post('http://localhost:8081/api/combinatorics/permutation', values);
            if (response.status !== 200) {
              throw new Error('Failed to fetch data');
          }
          
          else {
          setResult(response.data);
          }
  
      } catch (error) {
          setError(error.message);
      }
  
          },
        });
    
    return (
    <div className="Permutation" class="onepiece">
    <h3>Permutacje</h3>
    <form onSubmit={formik.handleSubmit}>
    Ile istnieje permutacji zbioru <label htmlFor="n">n=</label><input type="number" id="cn" name="n" value={formik.values.n} onChange={formik.handleChange} onBlur={formik.handleBlur}/>-elementowego? {result} <input type="submit" />
    <button type="button" onClick={formik.handleReset}>Resetuj</button>
   <div class="error">
    {formik.touched.n && formik.errors.n ? (
        <div className="error">{formik.errors.n}</div>
      ) : null}</div>
    </form>
    

</div>
);
}

export default Permutation;
