import { useFormik } from 'formik';
import React, { useState } from 'react';
import axios from 'axios';
import * as Yup from 'yup';

function ExtEuclidean() {

    const [result, setResult] = useState({"x": " ", "y": " ", "gcd": " " });
   
    const formik = useFormik ({

        initialValues: {
            a: '',
            b: '',
        },
        validationSchema: Yup.object().shape({
            a: Yup.number()
            .required("Wprowad warto a")
            .integer("Wprowad warto cakowit a"),
            b: Yup.number()
            .required("Wprowad warto b")
            .integer("Wprowad warto cakowit b"),
          }),
        onSubmit: async values => {
            

                try {

            const response = await axios.post('http://localhost:8081/api/numbertheory/exteucli',values);
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            else {
                setResult(response.data);
                }
        
            } catch (error) {
                setError(error.message);
            }
            
        },



    });

    return (
    <div className="exteuclidean"  class="onepiece">
   <h3>Rozszerzony algorytm Euklidesa</h3>
<form onSubmit={formik.handleSubmit}>
<input type="number" id="a" name="a" value={formik.values.a} onChange={formik.handleChange} onBlur={formik.handleBlur} />&times;{result.x}
+
<input type="number" id="b" name="b" value={formik.values.b} onChange={formik.handleChange} onBlur={formik.handleBlur} />&times;{result.y}
= {result.gcd}    <input type="submit" /><button type="button" onClick={formik.handleReset}>Resetuj</button>
</form>
    {formik.touched.a && formik.errors.a ? (
            <div className="error">{formik.errors.a}</div>
            ) : null}
            {formik.touched.b && formik.errors.b ? (
            <div className="error">{formik.errors.b}</div>
            ) : null}
</div>
);

}

export default ExtEuclidean;