import React from 'react';
import axios from 'axios';
import { useState } from 'react';

function Multiplication ({rowsA, columnsA, matrixA, A, rowsB, columnsB, matrixB, B, resultMatrix, setResultMatrix, setValidationError}) {


    const [error, setError] = useState(null);

    const handleSubmit = async (data) => {
        
        if(columnsA != rowsB) {
            setValidationError("Macierze musz mie zgodne wymiary");
        }
        else {

        try {
            const response = await axios.post('http://localhost:8081/api/linalg/multiplication', {

            matrixA: {
                rows: rowsA,
                columns: columnsA,
                matrix: matrixA, 
            },
            matrixB: {
               rows: rowsB,
               columns: columnsB,
               matrix: matrixB,
            }
            });
   
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            
            else {
                setResultMatrix(response.data);
            setValidationError('');
            }
    
        } catch (error) {
            setError(error.message);
        }
        
    }
    };



    return (

        <div>

            <button onClick={handleSubmit}>{A}x{B}</button>
            
        </div>

    )

}

export default Multiplication;