import React, { useState } from 'react';
import axios from 'axios';


function Trace({ rows, columns, matrix, name, setValidationError }) {

    const [result, setResult] = useState('');

    const [error, setError] = useState(null);

    const handleSubmit = async (event) => {
 

        if (rows != columns) {
            setValidationError(
                "Macierz musi by kwadratowa!"
            );
        } else {

        try {
            const response = await axios.post('http://localhost:8081/api/linalg/trace', {
               rows: rows,
               columns: columns,
               matrix: matrix,
            });
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            
            else {
            setResult(response.data);
            }
    
        } catch (error) {
            setError(error.message);
        }
    }
    
         
    
    };

    return (
        <div>
            <button onClick={handleSubmit}>tr{name}</button> = {result}
        </div>


    );


}

export default Trace;