import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function VariationWR() {
    const [result, setResult] = useState("  ");

    const formik = useFormik({
        initialValues: {
          n: '',
          k: '',
        },
        validationSchema: Yup.object().shape({
          n: Yup.number()
          .required("Wprowad warto n")
          .min(0,"n nie moe by  ujemne")
          .integer("Wprowad warto cakowit n"),
          k: Yup.number()
          .required("Wprowad warto k")
          .min(0,"k nie moe by  ujemne")
          .integer("Wprowad warto cakowit k"),
        }),
        onSubmit: async values => {

              try {
            const response = await axios.post('http://localhost:8081/api/combinatorics/variationwr', values);
            if (response.status !== 200) {
              throw new Error('Failed to fetch data');
          }
          
          else {
          setResult(response.data);
          }
  
      } catch (error) {
          setError(error.message);
      }
  
          },
        });

    return (
    <div className="Variation"  class="onepiece">
     <h3>Wariacje z powtrzeniami</h3>
    <form onSubmit={formik.handleSubmit}>
    Na ile sposobw moemy wybra cig <label htmlFor="com_k">k=</label><input type="number" id="k" name="k" onChange={formik.handleChange} value={formik.values.k} onBlur={formik.handleBlur} /> elementw z 
    <label htmlFor="com_n"> n=</label><input type="number" id="n" name="n" onChange={formik.handleChange} value={formik.values.n} onBlur={formik.handleBlur} /> elementw, jeeli wartoci mog si powtarza? {result} <input type="submit" />
     <button type="button" onClick={formik.handleReset}>Resetuj</button>
     {formik.touched.n && formik.errors.n ? (
        <div className="error">{formik.errors.n}</div>
      ) : null}
      {formik.touched.k && formik.errors.k ? (
        <div className="error">{formik.errors.k}</div>
      ) : null}
    </form>
 
</div>
);
}

export default VariationWR;
