import React, { useState } from 'react';
import axios from 'axios';


function Inverse({ rows, columns, matrix, name, resultMatrix, setResultMatrix, setValidationError}) {

    const [error, setError] = useState(null);


    const handleSubmit = async (event) => {
        event.preventDefault();

        if (rows != columns) {
            setValidationError(
                "Macierz musi by kwadratowa"
            );
        } else {

        try {
            const response = await axios.post('http://localhost:8081/api/linalg/det', {
               rows: rows,
               columns: columns,
               matrix: matrix,
            });
   
            if (response.data == 0) {
                setValidationError("Wyznacznik jest rwny 0");
            }

            else {

                try {
                    const response = await axios.post('http://localhost:8081/api/linalg/inverse', {
                       rows: rows,
                       columns: columns,
                       matrix: matrix,
                    });
                    if (response.status !== 200) {
                        throw new Error('Failed to fetch data');
                    }
                    
                    else {
                        setResultMatrix(response.data);
                    setValidationError('');
                    }
            
                } catch (error) {
                    setError(error.message);
                }
                
                }

        } catch (error) {
            console.log('Server responded with error:', error.response.data);
            console.log('Status code:', error.response.status);
        } 
    }
        
    };

     





            // } else {
            //     console.log('Empty response received from server.');
            // }
        // } catch (error) {
        //     if (error.response) {
        //         console.log('Server responded with error:', error.response.data);
        //         console.log('Status code:', error.response.status);
        //     } else if (error.request) {
        //         console.log('No response received:', error.request);
        //     } else {
        //         console.log('Error setting up the request:', error.message);
        //     }
        // }
    // }
    

    return (
        <div>
            <button onClick={handleSubmit}>{name}<sup>-1</sup></button>
        </div>


    )


}

export default Inverse;