import React, { useEffect } from 'react';
import { useFormik } from 'formik';

function MatrixForm({ rows, columns, matrix, onMatrixChange, setColumns, setRows }) {

  const formik = useFormik({
    initialValues: {
      matrix: matrix,
    },

    onReset: async () => {

      formik.handleReset;

      setColumns(3);

      setRows(3);

    }
  
  });


  useEffect(() => {
    formik.setValues({
      ...formik.values,
      matrix: matrix,
    });

  }, [matrix, rows, columns]);


  const handleCellChange = (event, rowIndex, columnIndex) => {
    const { value } = event.target;
    const updatedMatrix = formik.values.matrix.map((row, rIndex) =>
      rIndex === rowIndex
        ? row.map((cell, cIndex) => (cIndex === columnIndex ? parseFloat(value, 10) : cell))
        : row
    );
    formik.setFieldValue(`matrix.${rowIndex}.${columnIndex}`, parseFloat(value, 10));
    onMatrixChange(updatedMatrix);
  };

  return (
    <div class="onepiece">
    <div class="flex">
      <form>
        <table>
          <tbody>
            {formik.values.matrix.map((row, rowIndex) => (
              <tr key={rowIndex}>
                {row.map((cell, columnIndex) => (
                  <td key={columnIndex}>
                    <input
                      type="number"
                     
                      name={`matrix.${rowIndex}.${columnIndex}`}
                      value={formik.values.matrix[rowIndex][columnIndex]}
                      onChange={(e) => handleCellChange(e, rowIndex, columnIndex)}
                    />
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>

      </form>
      </div>
      <br/>
      <button type="button" onClick={formik.handleReset}>Resetuj</button>
    </div>
  );
}

export default MatrixForm;
