import React, { useState } from 'react';
import MatrixForm from './MatrixForm';
import Det from './Det';
import Addition from './Addition';
import Subtraction from './Subtraction';
import Multiplication from './Multiplication';
import Trace from './Trace';
import Inverse from './Inverse';
import Transposition from './Transposition';

function Matrices() {

 const [resultMatrix, setResultMatrix] = useState([]);
 const [validationError, setValidationError] = useState(null);

  const [rowsA, setRowsA] = useState(3);
  const [columnsA, setColumnsA] = useState(3);

  const [rowsB, setRowsB] = useState(3);
  const [columnsB, setColumnsB] = useState(3);


  const generateEmptyMatrix = (numRows, numColumns) => {
    const matrix = [];
    for (let i = 0; i < numRows; i++) {
      matrix.push(Array(numColumns).fill(0));
    }
    return matrix;
  };

  const [matrixA, setMatrixA] = useState(generateEmptyMatrix(rowsA, columnsA));
  const [matrixB, setMatrixB] = useState(generateEmptyMatrix(rowsB, columnsB));

  const handleRowsChangeA = (e) => {
    const newRowsA = parseInt(e.target.value, 10);
    setRowsA(newRowsA);
    setMatrixA(generateEmptyMatrix(newRowsA, columnsA));
  };

  const handleRowsChangeB = (e) => {
    const newRowsB = parseInt(e.target.value, 10);
    setRowsB(newRowsB);
    setMatrixB(generateEmptyMatrix(newRowsB, columnsB));
  };

  // Function to handle change in columns input
  const handleColumnsChangeA = (e) => {
    const newColumnsA = parseInt(e.target.value, 10);
    setColumnsA(newColumnsA);
    setMatrixA(generateEmptyMatrix(rowsA, newColumnsA));
  };

  const handleColumnsChangeB = (e) => {
    const newColumnsB = parseInt(e.target.value, 10);
    setColumnsB(newColumnsB);
    setMatrixB(generateEmptyMatrix(rowsB, newColumnsB));
  };

  const handleMatrixChangeA = (newMatrix) => {
    setMatrixA(newMatrix);
  };

  const handleMatrixChangeB = (newMatrix) => {
    setMatrixB(newMatrix);
  };
  


  return (
    <div>
    <h3>Macierze</h3>
    <div class="two_columns">
      <div class="column2">
      <div class="matrix">
          <h4>Macierz A</h4>
          <label>
            Liczba wierszy:
            <input type="number" value={rowsA} onChange={handleRowsChangeA} />
          </label>
          <label>
          Liczba kolumn:
            <input type="number" value={columnsA} onChange={handleColumnsChangeA} />
          </label>
          <MatrixForm rows={rowsA} columns={columnsA} matrix={matrixA} onMatrixChange={handleMatrixChangeA}  setColumns={setColumnsA} setRows={setRowsA} />
          </div>
          <div class="buttons">
          <Det rowsA={rowsA} columnsA={columnsA} matrixA={matrixA} name='A' setValidationError={setValidationError}/>
          <Trace rows={rowsA} columns={columnsA} matrix={matrixA} name='A' setValidatioError={setValidationError}/>
        </div>
      </div>
      <div class="column2">
      <div class="matrix">
          <h4>Macierz B</h4>
          <label>
          Liczba wierszy:
            <input type="number" value={rowsB} onChange={handleRowsChangeB} />
          </label>
          <label>
          Liczba kolumn:
            <input type="number" value={columnsB} onChange={handleColumnsChangeB} />
          </label>
        <MatrixForm rows={rowsB} columns={columnsB} matrix={matrixB} onMatrixChange={handleMatrixChangeB} setColumns={setColumnsB} setRows={setRowsB}/>
        </div>
        <div class="buttons">
        <Det rowsA={rowsB} columnsA={columnsB} matrixA={matrixB} name='B' setValidationError={setValidationError}/>
        <Trace rows={rowsB} columns={columnsB} matrix={matrixB} name='B' setValidationError={setValidationError}/>
        </div>
      </div>
      
    </div>
    <div class="buttons">
      <Transposition rows={rowsA} columns={columnsA} matrix={matrixA} name='A' setResultMatrix={setResultMatrix}/>
      <Transposition rows={rowsB} columns={columnsB} matrix={matrixB} name='B' setResultMatrix={setResultMatrix}/>
      <Addition rowsA={rowsA} columnsA={columnsA} matrixA={matrixA} rowsB={rowsB} columnsB={columnsB} matrixB={matrixB} validationError={validationError} resultMatrix={resultMatrix} setValidationError={setValidationError} setResultMatrix={setResultMatrix} />   
      <Subtraction A='A' B='B' rowsA={rowsA} columnsA={columnsA} matrixA={matrixA} rowsB={rowsB} columnsB={columnsB} matrixB={matrixB} validationError={validationError} resultMatrix={resultMatrix} setValidationError={setValidationError} setResultMatrix={setResultMatrix} />   
      <Subtraction A='B' B='A' rowsA={rowsB} columnsA={columnsB} matrixA={matrixB} rowsB={rowsA} columnsB={columnsA} matrixB={matrixA} validationError={validationError} resultMatrix={resultMatrix} setValidationError={setValidationError} setResultMatrix={setResultMatrix} />
      <Multiplication A='A' B='B' rowsA={rowsA} columnsA={columnsA} matrixA={matrixA} rowsB={rowsB} columnsB={columnsB} matrixB={matrixB} validationError={validationError} resultMatrix={resultMatrix} setValidationError={setValidationError} setResultMatrix={setResultMatrix} />   
      <Multiplication A='B' B='A' rowsA={rowsB} columnsA={columnsB} matrixA={matrixB} rowsB={rowsA} columnsB={columnsA} matrixB={matrixA} validationError={validationError} resultMatrix={resultMatrix} setValidationError={setValidationError} setResultMatrix={setResultMatrix} />   
      <Inverse rows={rowsA} columns={columnsA} matrix={matrixA} name='A' resultMatrix={resultMatrix} setResultMatrix={setResultMatrix} setValidationError={setValidationError}/>
      <Inverse rows={rowsB} columns={columnsB} matrix={matrixB} name='B' resultMatrix={resultMatrix} setResultMatrix={setResultMatrix} setValidationError={setValidationError}/>
    </div>
    <br />

      <div class="error">{validationError}</div>
    <div class="flex">

      <div className="matrix-content" class="matrix">
    {resultMatrix.map((row, rowIndex) => (
        <div key={rowIndex}>
          {row.map((col, colIndex) => (
            <span key={colIndex}>{col} </span>
          ))}
        </div>
      ))}
      </div>
   
    </div>

    </div>
  );
}

export default Matrices;
