import React, { useState } from 'react';
import { useFormik } from 'formik';
import axios from 'axios';
import * as Yup from 'yup';

function Conversions() {
    const [result, setResult] = useState('');
 

    const formik = useFormik({

        initialValues: {
            base1: 2,
            base2: 2,
            string: '',
        },

        validationSchema: Yup.object().shape({
            string: Yup.string()
            .required("Wpisz liczb")
            .test('valid-characters', "Wpisz poprawn liczb", (value, context) => {
                const base = context.parent.base1;
                let regex;
                if (base === 16) {
                    regex = /^[0-9a-fA-F]*$/;
                } else {
                    const allowedCharacters = [];
                    for (let i = 0; i < base; i++) {
                      allowedCharacters.push(i.toString());
                    }
                    regex = new RegExp(`^[${allowedCharacters.join('')}]*$`);
                }
                console.log(base);
                console.log(regex);
                return regex.test(value);
              }),
            }),

        onSubmit: async values => {

            try {

            const response = await axios.post('http://localhost:8081/api/numsys/conv',values);

            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            else {
            setResult(response.data);
            }
            }
            catch (error) {
                setError(error.message);
            }
        }

    });

    return (
        <div className="onepiece">
            <h3>Konwersja</h3>
            <form onSubmit={formik.handleSubmit}>
                <div className="three_columns">
                    <div className="column">
                        <select id="base1" name="base1" value={formik.values.base1} onChange={formik.handleChange}>
                        <option disabled selected>wybierz baz</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="16">16</option>
                        </select><br/><br />
                        <input type="text" id="string" name="string" value={formik.values.string} onChange={formik.handleChange}/>
                    </div>
                    <div className="middle_column">
                        &rarr;
                        <br/><br/>
                        &rarr;
                    </div>
                    <div className="column">
                    <select id="base2" name="base2" value={formik.values.base2} onChange={formik.handleChange}>
                    <option disabled selected>wybierz baz</option>
                        <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="16">16</option>
                        </select>
                        <br/><br/>
                        {result}
                    </div>
                </div>
                <br/>
                <input type="submit"/>
                <button type="button" onClick={formik.handleReset}>Resetuj</button>
                {formik.touched.string && formik.errors.string ? (
            <div className="error">{formik.errors.string}</div>
            ) : null}
            </form>
            {/* {error && <div>Error: {error}</div>} */}
        </div>
    );
}

export default Conversions;
