import React from "react";
import About from "./about";

function Main({setOption}) {


    const setCombinatorics = () => setOption("Combinatorics");
    const setNumberTheory = () => setOption("NumberTheory");
    const setLinearAlgebra = () => setOption("LinearAlgebra");
    const setNumericalSystems = () => setOption("NumericalSystems");

return (
<div>
    <div class="onepiece">
    <h3>Menu</h3>
    <div class="mainmenu">

        <button value="combinatorics" onClick={setCombinatorics} class="menu_button">kombinatoryka</button>

        <button onClick={setNumberTheory} class="menu_button">teoria liczb</button>

        <button onClick={setNumericalSystems} class="menu_button">systemy liczbowe</button>

        <button onClick={setLinearAlgebra} class="menu_button">macierze</button>


    </div>
    </div>
    <About />
    
    </div>




);


}

export default Main;