import React from 'react';
import axios from 'axios';
import { useState } from 'react';


function Transposition({ rows, columns, matrix, name, setResultMatrix}) {

  
    const [error, setError] = useState(null);

    const handleSubmit = async (event) => {
        event.preventDefault();

        try {
            const response = await axios.post('http://localhost:8081/api/linalg/transposition', {
               rows: rows,
               columns: columns,
               matrix: matrix,
            });
   
            if (response.status !== 200) {
                throw new Error('Failed to fetch data');
            }
            
            else {
            setResultMatrix(response.data);
            setValidationError('');
            }
    
        } catch (error) {
            setError(error.message);
        }

    
    };

    return (
        <div>
            <button onClick={handleSubmit}>{name}<sup>T</sup></button>
        </div>


    )


}

export default Transposition;